label {
  font-weight: normal;
  &.control-label{
  	margin-bottom: 0;
  }
}
.form-control{
	padding: @padding-small-vertical @padding-base-horizontal;
}

.input-group-addon{
	padding: 0 @padding-base-horizontal;
	background: #fff;
	color: @text-color;
	font-size: @font-size-base;
}
.form-control-feedback{
	color: @icon-color;
	font-size: @font-size-small;
}
.form-clear-input{
	box-shadow: none;
	color: @text-color;
	font-size:20px;
}
.form-group {
  margin-bottom: @padding-base-horizontal;
}
.input-group{
	.glyphicon{
		color: @icon-color;
		font-size: @font-size-small;
	}
}
.input-textarea{
	width: 100%;
	border: 1px solid @gray-lighter;
	border-radius: @border-radius-base;
	padding: @padding-small-vertical @padding-base-horizontal;
    &:focus {
      border-color: @brand-primary;
      box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 6px lighten(@brand-primary, 20%);
    }
}
.form-control.disabled{
	.form-control-feedback{
		color: @text-disabled-color;
	}
}
.form-control-static{
	color: @text-weak-color;
	word-wrap:break-word;
	min-height: 1px;
	p{
		margin: 0;
		padding: 0;
	}
}
.input-group-ip{
	.form-ip-addon{
		display: table-cell;
		position: relative;
		.form-control{
			outline: none;
			box-shadow: none;
		}
		&:first-child{
			.form-control{
				border-radius: @border-radius-base 0 0 @border-radius-base;
			}
		}
		&:last-child{
			.form-control{
				border-radius: 0 @border-radius-base @border-radius-base 0;
			}
		}
		&:not(:first-child){
			.form-control{
				border-left: none;
			}
		}
		&:not(:last-child){
			&:after{
				content: '';
				position: absolute;
				right: 0;
				bottom: @padding-small-vertical;
				display: block;
				width: 2px;
				height: 2px;
				background: @border-color;
				z-index: 2;
			}
			.form-control{
				border-right: none;
			}
		}
	}
}

.form-horizontal {
	.radio,
	.checkbox,
	.radio-inline,
	.checkbox-inline {
		margin-top: 0;
		margin-bottom: 0;
		padding-top: (@padding-small-vertical + 1); // Default padding plus a border
	}
	.form-control-static{
		padding-bottom: 0;
		padding-top: (@padding-small-vertical + 1);
	}
	&.label-text-left{
		.control-label{
	      text-align: left;
	    }
	}
	@media (min-width: @screen-xs-min) {
	    .control-label{
	      padding-top: (@padding-small-vertical + 1); // Default padding plus a border
	      text-align: right;
	    }
    }
}